import { _ } from '@splunk/ui-utils/i18n';

export const itemsSchema = {
    items: {
        description: _('Specify the label text that appears below the value.'),
        anyOf: [
            {
                type: 'array',
                items: {
                    type: 'object',
                    properties: {
                        label: {
                            description: _(
                                'The text displayed in the dropdown menu'
                            ),
                            type: 'string',
                            minLength: 1,
                            pattern: '.*\\S.*',
                        },
                        value: {
                            description: _(
                                'The value used when the menu item is selected'
                            ),
                            type: 'string',
                            minLength: 1,
                            pattern: '.*\\S.*',
                        },
                    },
                    required: ['value'],
                },
            },
            {
                type: 'string',
                pattern: '^>.*',
            },
        ],
    },
};

export const selectFirstSearchResult = ({ value = false } = {}) => ({
    selectFirstSearchResult: {
        description: _(
            'Select the first result of an attached search as default.'
        ),
        type: 'boolean',
        default: false,
        const: value,
    },
});
