import React, { useMemo, useRef, useEffect } from 'react';
import { debounce } from 'lodash';
import { _ } from '@splunk/ui-utils/i18n';
import SUIButton from '@splunk/react-ui/Button';
import { noop } from '@splunk/dashboard-utils';
import type { InputStatics } from '@splunk/dashboard-types';
import { withInputWrapper } from '../utils/enhancer';
import schema from './ButtonSchema';
import type { InputProps } from '../types';

interface ButtonInputOptions {
    label?: string;
}

const defaultOptions = {};

const ButtonInput: React.ComponentType<InputProps> & InputStatics = ({
    options: { label } = defaultOptions,
    onValueChange = noop,
    isSelected = false,
}: InputProps<ButtonInputOptions>) => {
    const handleChange = useMemo(
        () => debounce(() => onValueChange(), 250),
        [onValueChange]
    );
    const inputRef = useRef<HTMLButtonElement | null>(null);
    useEffect(() => {
        // When input is selected, make sure we focus the button
        if (isSelected && inputRef.current) {
            inputRef.current.focus({ preventScroll: true });
        }
    }, [isSelected]);

    return (
        <SUIButton
            label={label || _('Button')}
            onClick={handleChange}
            elementRef={inputRef}
            inline={false}
        />
    );
};

const editor = [
    {
        label: _('Label'),
        enableCollapsible: true,
        layout: [
            [
                {
                    label: _('Label'),
                    option: 'label',
                    editor: 'editor.text',
                },
            ],
        ],
    },
];

ButtonInput.config = {
    editorConfig: editor,
    optionsSchema: schema,
    label: _('Button'),
    description: _('Interactive button'),
    showTitle: false,
    showToken: false,
    withInputLabel: false,
    fitToWidth: true,
};

export default withInputWrapper(ButtonInput);
