import React, {
    useMemo,
    useState,
    useCallback,
    useEffect,
    useRef,
} from 'react';
import styled, { css } from 'styled-components';
import { _ } from '@splunk/ui-utils/i18n';
import { createDOMID } from '@splunk/ui-utils/id';
import { useFeatureFlags } from '@splunk/dashboard-context';
import type { InputStatics } from '@splunk/dashboard-types';
import {
    InputTitle,
    applyTranslucence,
    customThemeVariables,
} from '@splunk/dashboard-ui';
import { noop } from '@splunk/dashboard-utils';
import Button, { type ButtonClickHandler } from '@splunk/react-ui/Button';
import Cross from '@splunk/react-icons/Cross';
import ScreenReaderContent from '@splunk/react-ui/ScreenReaderContent';
import { pickVariant, variables } from '@splunk/themes';
import type { InputProps, HOCProps } from '../types';
import { InputStatusIcon } from '../components/InputStatusIcon';

// todo: change them back to the original values above, after the react-sortable-hoc supporting variable width in grid https://github.com/clauderic/react-sortable-hoc#grid-support
export const SHORT_WIDTH_DRAGGABLE = 198;
export const LONG_WIDTH_DRAGGABLE = 198;

interface InputContainerProps {
    showSelection: boolean;
    mode: 'edit' | 'view';
    width: number | 'fit-content';
    $maxWidth?: number;
    isOnCanvas: boolean;
    canBeHidden?: boolean;
    showBorder: boolean;
}

const GridInput = styled.div`
    grid-area: input;
`;

export const InputContainer = styled.div.attrs<InputContainerProps>(
    ({ width, $maxWidth }) => ({
        'data-test': 'input-container',
        style: {
            width,
            maxWidth: $maxWidth,
        },
    })
)<InputContainerProps>`
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto;
    grid-template-areas:
        'title status remove'
        'input input input';
    row-gap: 8px;
    align-items: center;
    padding: 8px;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid;
    position: relative;
    background-color: ${pickVariant<InputContainerProps>('showSelection', {
        true: variables.interactiveColorOverlaySelected,
        false: 'transparent',
    })};
    border-style: ${pickVariant<InputContainerProps>('canBeHidden', {
        true: 'dashed',
        false: 'solid',
    })};
    border-color: ${pickVariant<InputContainerProps>('showBorder', {
        true: pickVariant<InputContainerProps>('showSelection', {
            // selected:
            true: variables.focusColor,
            // can be hidden but not selected:
            false: applyTranslucence(variables.interactiveColorBorder, 0.7),
        }),
        false: 'transparent',
    })};
    &:hover {
        background-color: ${({ isOnCanvas, mode }) =>
            isOnCanvas || mode === 'view'
                ? 'transparent'
                : variables.interactiveColorOverlaySelected};
        border-color: ${pickVariant<InputContainerProps>('showBorder', {
            true: pickVariant<InputContainerProps>('showSelection', {
                false: variables.interactiveColorBorder,
            }),
            false: 'transparent',
        })};
    }
    & > ${GridInput} {
        ${pickVariant<InputContainerProps>('isOnCanvas', {
            true: {
                enterprise: css`
                    & > * {
                        background-color: ${customThemeVariables.dashboardBackgroundColor};
                    }
                `,
            },
        })};
    }

    & > ${GridInput} {
        ${pickVariant<InputContainerProps>('isOnCanvas', {
            true: {
                enterprise: css<InputContainerProps>`
                    box-shadow: inset 0px 0px 0px ${variables.inputHeight}
                        ${customThemeVariables.dashboardBackgroundColor};
                    clip-path: inset(0 round ${variables.borderRadius});
                `,
            },
        })};
    }
`;

const InputDragHandle = styled.button.attrs({
    'data-test': 'input-drag-handle',
})`
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 8px auto;
    width: 0;
    height: 0;
    /* background gradient dots */
    background: ${variables.draggableBackground};
    cursor: move;
    border: none;
    padding: 0;

    :focus,
    ${InputContainer}:hover & {
        width: 30px;
        height: 15px;
    }
`;

const InputRemoveIcon = (
    <>
        <Cross width="11px" height="11px" />
        <ScreenReaderContent>{_('Delete input')}</ScreenReaderContent>
    </>
);

interface InputRemoveProps {
    isSelected?: boolean;
}

const InputRemove = styled(Button).attrs({
    'data-test': 'input-remove',
})<InputRemoveProps>`
    height: 16px;
    width: 16px;
    visibility: ${(props) => (props.isSelected ? 'visible' : 'hidden')};
    flex-grow: 0;
    min-height: 0;
    min-width: 0;
    padding: 0;
    grid-area: remove;
    justify-self: end;
`;

const GridTitle = styled.div`
    grid-area: title;
    overflow: hidden;
    white-space: nowrap;
    height: 16px;
`;

const GridStatus = styled.div`
    grid-area: status;
    // eliminate padding from SimpleStatusIcon
    margin-right: -12px;
`;

const empty = {};
const noopValidator = (): { errorMessages: string[] } => ({
    errorMessages: [],
});

export const UNSUPPORTED_TYPE_MSG = _(
    'This input does not support the received value type'
);

type HOC = ((props: HOCProps) => JSX.Element) & InputStatics;

/**
 * HOC that wraps the Input component in a container displaying input title, error icon if error is present
 * and remove button if selected in edit mode. Also sets container style (backgroundColor, border) based on mode and isSelected
 * @param {ReactElement} InputComponent Input Component
 * @param {String} mode Dashboard mode - edit or view
 * @param {String} title Input title
 * @param {String} id Input id
 * @param {Object} options Input options
 * @param {Boolean} isSelected Input is selected in edit mode or not
 * @param {Number} width Input Container width - TRP would override this
 */
export const withInputWrapper = (
    InputComponent: React.ComponentType<InputProps> & InputStatics
): HOC => {
    const useValueTypeCheck: (value: unknown) => boolean =
        typeof InputComponent.canHandleValue === 'function'
            ? InputComponent.canHandleValue
            : () => true;

    const WrappedComponent = ({
        mode = 'view',
        title,
        id,
        attributes,
        listeners,
        options = empty,
        isSelected = false,
        width = SHORT_WIDTH_DRAGGABLE,
        onRemove = noop,
        dataSources = empty,
        isOnCanvas = false,
        canBeHidden = false,
        value,
        containerOptions = empty,
        ...rest
    }: HOCProps) => {
        const [currentWidth, setCurrentWidth] = useState(width);
        const { enableDragDropInputs } = useFeatureFlags();
        const validator = InputComponent.validate ?? noopValidator;
        const isFirstRenderRef = useRef(true);

        const isValueTypeSupported = useValueTypeCheck(
            value ?? options.defaultValue
        );
        const inputValue = isValueTypeSupported ? value : undefined;

        useEffect(() => {
            // The reason we need to avoid setting it on first render is that it
            //   overwrites the call to `onUpdateWidth` from components such as TRP
            if (!isFirstRenderRef.current) {
                setCurrentWidth(width);
            }
            isFirstRenderRef.current = false;
        }, [width]);

        const { validationMessage, warningMessage } = useMemo(() => {
            const messages: {
                validationMessage?: string;
                warningMessage?: string;
            } = {};

            const { errorMessages } = validator({ options }) ?? {};
            if (Array.isArray(errorMessages)) {
                messages.validationMessage = errorMessages.join(', ');
            } else {
                messages.validationMessage = errorMessages;
            }

            if (!isValueTypeSupported) {
                messages.warningMessage = UNSUPPORTED_TYPE_MSG;
            }

            return messages;
        }, [validator, options, isValueTypeSupported]);

        const handleRemove = useCallback<ButtonClickHandler>(
            (e) => {
                onRemove(id);
                e.stopPropagation();
            },
            [onRemove, id]
        );

        const isDraggable =
            enableDragDropInputs &&
            mode === 'edit' &&
            isSelected &&
            !isOnCanvas;

        const dragHandleEl = useMemo<React.ReactNode>(
            () =>
                isDraggable && (
                    <InputDragHandle {...attributes} {...listeners} />
                ),
            [attributes, isDraggable, listeners]
        );

        const isGlobalEditModeInput = mode === 'edit' && !isOnCanvas;
        const showSelection = isSelected && isGlobalEditModeInput;
        const showBorder = isGlobalEditModeInput && (isSelected || canBeHidden);
        const {
            showTitle,
            withInputLabel,
            fitToWidth: fitToWidthConfig,
        } = InputComponent.config ?? {};

        const fitToWidth = !isOnCanvas && fitToWidthConfig;

        const titleId = useMemo(() => createDOMID(), []);

        return (
            <InputContainer
                mode={mode}
                width={fitToWidth ? 'fit-content' : currentWidth}
                $maxWidth={fitToWidth ? currentWidth : undefined}
                showSelection={showSelection}
                showBorder={showBorder}
                isOnCanvas={isOnCanvas}
                canBeHidden={canBeHidden}
            >
                <GridTitle>
                    <InputTitle
                        titleId={titleId}
                        title={showTitle === false ? undefined : title || ''}
                        inputId={id}
                        containerOptions={containerOptions}
                        withInputLabel={withInputLabel}
                    />
                </GridTitle>
                <GridInput>
                    <InputComponent
                        id={id}
                        isSelected={isSelected}
                        options={options}
                        onUpdateWidth={isOnCanvas ? noop : setCurrentWidth}
                        dataSources={dataSources}
                        value={inputValue}
                        title={title}
                        titleId={titleId}
                        {...rest}
                    />
                </GridInput>
                {dragHandleEl}
                <GridStatus>
                    <InputStatusIcon
                        dataSource={dataSources?.primary}
                        componentId={id}
                        validationMessage={validationMessage}
                        warningMessage={warningMessage}
                    />
                </GridStatus>
                {isGlobalEditModeInput && (
                    <InputRemove
                        appearance="subtle"
                        icon={InputRemoveIcon}
                        onClick={handleRemove}
                        isSelected={isSelected}
                    />
                )}
            </InputContainer>
        );
    };

    // TODO: use hoist-non-react-statics instead of hardcoded values?
    WrappedComponent.canHandleValue = InputComponent.canHandleValue;
    WrappedComponent.valueToTokens = InputComponent.valueToTokens;
    WrappedComponent.tokensToValue = InputComponent.tokensToValue;
    WrappedComponent.validate = InputComponent.validate;
    WrappedComponent.config = InputComponent.config;

    return WrappedComponent;
};
