import React, { useRef, useEffect } from 'react';
import { _ } from '@splunk/ui-utils/i18n';
import SUINumber, {
    type NumberPropsBaseControlled,
    type NumberPropsBaseUncontrolled,
} from '@splunk/react-ui/Number';
import { NumberInput as NumberIcon } from '@splunk/dashboard-icons';
import { useNumberInput } from '@splunk/dashboard-ui';
import { noop } from '@splunk/dashboard-utils';
import type { InputStatics } from '@splunk/dashboard-types';
import styled, { css } from 'styled-components';
import { pickVariant, variables } from '@splunk/themes';
import type { ComponentProps } from '@splunk/react-ui/types/src/utils/types';
import { withInputWrapper } from '../utils/enhancer';
import schema from './NumberSchema';
import { validateTokenName } from '../utils/validate';
import type { InputProps } from '../types';

const filterNaN = <T,>(value: T): T | undefined =>
    Number.isNaN(value) ? undefined : value;

interface NumberInputOptions {
    min?: number;
    max?: number;
    step?: number;
    defaultValue?: number;
}

const defaultOptions = {};

const ErrorBorder = css`
    border-color: ${variables.interactiveColorAccentError};
    &,
    & * {
        border-color: ${variables.interactiveColorAccentError};
    }
    color: ${variables.interactiveColorAccent};
`;

const StyledNumber = styled(SUINumber)`
    ${pickVariant<
        ComponentProps<
            NumberPropsBaseControlled | NumberPropsBaseUncontrolled,
            'div'
        >
    >('error', {
        true: ErrorBorder,
    })}
`;

const NumberInput: React.ComponentType<InputProps> & InputStatics = ({
    id,
    options: { min, max, step, defaultValue } = defaultOptions,
    value,
    onValueChange = noop,
    isDisabled = false,
    disabledMessage,
    errorMessage,
    isError = false,
    isSelected = false,
    title,
}: InputProps<NumberInputOptions>) => {
    const {
        currentValue,
        handleChange,
        handleBlur,
        handleFocus,
        handleKeyPress,
    } = useNumberInput({ value, defaultValue, onValueChange });

    // if there is no value show input as blank by passing undefined
    const displayValue =
        currentValue != null ? Number(currentValue) : undefined;
    const inputRef = useRef<HTMLInputElement | null>(null);

    let message;
    if (isError) {
        message = errorMessage;
    } else if (isDisabled) {
        message = disabledMessage;
    }

    useEffect(() => {
        // When input is selected, make sure we focus the textbox
        if (isSelected && !isDisabled && !!inputRef.current) {
            inputRef.current.focus({ preventScroll: true });
        }
    }, [isSelected, isDisabled]);

    return (
        <StyledNumber
            min={min}
            max={max}
            step={step}
            error={isError}
            disabled={isDisabled}
            inputId={id}
            onChange={handleChange}
            onBlur={handleBlur}
            onFocus={handleFocus}
            onKeyDown={handleKeyPress}
            // we want to show disabled message if the input is disabled
            value={message ? undefined : filterNaN(displayValue)}
            placeholder={message}
            inputRef={inputRef}
            aria-label={title}
        />
    );
};

const editor = [
    {
        label: _('Token'),
        enableCollapsible: true,
        layout: [
            [
                {
                    label: _('Token name'),
                    option: 'token',
                    editor: 'editor.text',
                    editorProps: {
                        validate: validateTokenName,
                    },
                },
            ],
        ],
    },
    {
        label: _('Value'),
        enableCollapsible: true,
        layout: [
            [
                {
                    label: _('Min'),
                    option: 'min',
                    editor: 'editor.number',
                },
                {
                    label: _('Max'),
                    option: 'max',
                    editor: 'editor.number',
                },
                {
                    label: _('Step'),
                    option: 'step',
                    editor: 'editor.number',
                    editorProps: {
                        min: 1,
                    },
                },
            ],
            [
                {
                    label: _('Default value'),
                    option: 'defaultValue',
                    editor: 'editor.number',
                },
            ],
        ],
    },
];

NumberInput.config = {
    editorConfig: editor,
    optionsSchema: schema,
    label: _('Number'),
    description: _('Field to enter a number'),
    baseShape: {
        options: {
            defaultValue: 10,
        },
        title: _('Number Input Title'),
    },
    tokenPrefix: 'num',
    icon: NumberIcon,
    withInputLabel: true,
};

/**
 * Allow the input to determine if it can or cannot handle the provided value
 * @param {unknown} value Input value
 * @returns `true` if the input can handle and render with the provided value
 */
NumberInput.canHandleValue = (value) => {
    if (value === null || typeof value === 'undefined') {
        // Support no value
        return true;
    }

    if (typeof value === 'number' || typeof value === 'string') {
        // Support finite numeric strings or number literals
        return Number.isFinite(Number(value));
    }

    // Type is unsupported
    return false;
};

/**
 * Transforms the value or values from the input to a set of token: value pairs
 * @param {String} value value of the number input
 * @param {Object} meta
 * @param {String} meta.token The token name
 * @returns {Object}
 */
NumberInput.valueToTokens = (value, { token }) => {
    if (!token) {
        return {};
    }
    if (value == null) {
        return {
            [token]: null,
        };
    }
    return {
        [token]: `${value}`,
    };
};

/**
 * Transforms a set of tokens belonging to the input to a valid input value
 * @param {Object} tokens set of submitted tokens
 * @param {String} tokenName the token name associated with the input
 * @param {String} tokenNamespace the namespace the token belongs to
 * @returns {Number|null}
 */
NumberInput.tokensToValue = ({ tokens, tokenName, tokenNamespace }) => {
    if (!tokens || !tokenName || !tokenNamespace) {
        return null;
    }
    const tokenValue = tokens[tokenNamespace]?.[tokenName];
    if (!tokenValue) {
        // needed because Number(null) resolves to 0
        return null;
    }
    // if token is a string, convert to number
    return Number(tokenValue);
};

/**
 * validates the static input definition - options/context and returns all the error messages accordingly
 * if no error is found, returns an empty object with errorMessages set to []
 * @param {Object} [inputDef={}]
 * @param {Object} [inputDef.options] input options
 * @returns {Object}
 */
NumberInput.validate = (inputDef: { options?: NumberInputOptions }) => {
    const options = inputDef.options ?? {};
    const errorMessages = [];
    const { min, max } = options;
    if (min !== undefined && max !== undefined && min > max) {
        errorMessages.push('min should be less than max');
    }
    return { errorMessages };
};

export default withInputWrapper(NumberInput);
