import React, { useCallback, useMemo, useRef, useEffect } from 'react';
import { _ } from '@splunk/ui-utils/i18n';
import SUIText from '@splunk/react-ui/Text';
import { Text as TextIcon } from '@splunk/dashboard-icons';
import { useTextInput } from '@splunk/dashboard-ui';
import { isPrimitive, noop } from '@splunk/dashboard-utils';
import type { InputStatics } from '@splunk/dashboard-types';
import { withInputWrapper } from '../utils/enhancer';
import TextSchema from './TextSchema';
import { validateTokenName } from '../utils/validate';
import type { InputProps } from '../types';

const TextInputIcon = (props: React.ComponentProps<typeof TextIcon>) => (
    <TextIcon screenReaderText={_('Text Input')} {...props} />
);

interface TextInputOptions {
    placeholder?: string;
    defaultValue?: string;
}

const defaultOptions = {};

/**
 * We use SUI Text under the hood to render the input
 */
const Text: React.ComponentType<InputProps> & InputStatics = ({
    id,
    options: { placeholder: customPlaceholder, defaultValue } = defaultOptions,
    value: committedValue,
    onValueChange = noop,
    isDisabled = false,
    disabledMessage,
    errorMessage,
    isError = false,
    isSelected = false,
    title,
}: InputProps<TextInputOptions>) => {
    const inputRef = useRef<HTMLInputElement | null>(null);
    const value = committedValue?.toString() ?? '';
    let placeholder = customPlaceholder ?? '';
    if (errorMessage) {
        placeholder = errorMessage;
    } else if (disabledMessage) {
        placeholder = disabledMessage;
    }
    const uniqKey = useMemo(() => ({ id }), [id]);
    const handleCommit = useCallback(
        (val: string) => onValueChange(null, val),
        [onValueChange]
    );

    const {
        onHandleBlur,
        onHandleChange,
        onHandleKeyDown,
        value: inputValue,
    } = useTextInput({
        initialValue: value,
        handleCommit,
        uniqKey,
        defaultValue,
        controlledValue: value,
    });

    useEffect(() => {
        // When input is selected, make sure we focus the textbox
        if (isSelected && !isDisabled && !!inputRef.current) {
            inputRef.current.focus({ preventScroll: true });
        }
    }, [isSelected, isDisabled]);

    return (
        <SUIText
            canClear
            inputId={id}
            placeholder={placeholder}
            disabled={isDisabled}
            error={isError}
            value={inputValue}
            onChange={onHandleChange}
            onKeyDown={onHandleKeyDown}
            onBlur={onHandleBlur}
            inputRef={inputRef}
            aria-label={title}
        />
    );
};

/**
 * Allow the input to determine if it can or cannot handle the provided value
 * @param {unknown} value Input value
 * @returns `true` if the input can handle and render with the provided value
 */
Text.canHandleValue = isPrimitive;

/**
 * Transforms the value from the input to a token: value pair
 * @param {String} value Text input value
 * @param {Object} meta
 * @param {String} meta.token The token name
 * @param {String} meta.prefix Content that prepends the value
 * @param {String} meta.suffix Content that appends the value
 * @returns {Object}
 */
Text.valueToTokens = (value, { token, prefix = '', suffix = '' }) => {
    if (!token) {
        return {};
    }
    if (!value) {
        return {
            [token]: null,
        };
    }
    return {
        [token]: `${prefix}${value}${suffix}`,
    };
};

const editor = [
    {
        label: _('Token'),
        enableCollapsible: true,
        layout: [
            [
                {
                    label: _('Token name'),
                    option: 'token',
                    editor: 'editor.text',
                    editorProps: {
                        validate: validateTokenName,
                    },
                },
            ],
        ],
    },
    {
        label: _('Value'),
        enableCollapsible: true,
        layout: [
            [
                {
                    label: _('Default value'),
                    option: 'defaultValue',
                    editor: 'editor.text',
                },
            ],
        ],
    },
];

Text.config = {
    editorConfig: editor,
    optionsSchema: TextSchema,
    label: _('Text'),
    description: _('Field to enter text'),
    baseShape: {
        options: {
            defaultValue: _('Default Text'),
        },
        title: 'Text Input Title',
    },
    tokenPrefix: 'text',
    icon: TextInputIcon,
    withInputLabel: true,
};

export default withInputWrapper(Text);
