import React, { useRef } from 'react';
import {
    usePopoverState,
    EnterprisePopoverContentFactory,
    PopoverStatusIcon,
    getIconStatus,
    getStatusMessage,
} from '@splunk/dashboard-ui';
import type { DataSourceEventPayload } from '@splunk/dashboard-types';

interface InputStatusIconProps {
    dataSource?: DataSourceEventPayload;
    validationMessage?: string;
    warningMessage?: string;
    componentId: string;
}

export const InputStatusIcon = ({
    dataSource,
    validationMessage,
    warningMessage,
    componentId,
}: InputStatusIconProps): JSX.Element | null => {
    const anchor = useRef<HTMLElement | null>(null);
    const { open, onRequestOpen, onRequestClose } = usePopoverState({
        stopPropagation: true,
        anchor,
    });

    let iconStatus = getIconStatus(dataSource);
    if (warningMessage) {
        iconStatus = 'warning';
    }

    if (validationMessage) {
        iconStatus = 'error';
    }

    if (!iconStatus) {
        return null;
    }

    const hasValidatorMessage =
        Boolean(validationMessage) || Boolean(warningMessage);

    const hasStatusMessage = !!getStatusMessage(dataSource);

    const hasContent = hasValidatorMessage || hasStatusMessage;

    return (
        <PopoverStatusIcon
            height={16}
            iconSize={16}
            iconSizeOnHover={18}
            open={open}
            onRequestOpen={onRequestOpen}
            onRequestClose={onRequestClose}
            status={iconStatus}
            anchor={anchor}
            hasContent={hasContent}
        >
            <EnterprisePopoverContentFactory
                dataSource={dataSource}
                componentId={componentId}
                onRequestClose={onRequestClose}
                message={
                    validationMessage && validationMessage !== ''
                        ? validationMessage
                        : warningMessage
                }
                type="input"
            />
        </PopoverStatusIcon>
    );
};
