import { baseInputSchema } from '../components/BaseInput';
import {
    itemsSchema,
    selectFirstSearchResult,
} from '../components/DropdownItemsSchema';

const commonProperties = {
    ...baseInputSchema,
    ...itemsSchema,
    prefix: {
        description: 'Text that will be prepended to the selected value',
        type: 'string',
    },
    suffix: {
        description: 'Text that will be concatenated to the selected value',
        type: 'string',
    },
};

export default {
    // extend means we are defining not just the properties, but the entire option schema itself
    extend: {
        type: 'object',
        anyOf: [
            {
                properties: {
                    ...selectFirstSearchResult({ value: false }),
                    defaultValue: {
                        description:
                            'Default value of the input when no value is specified',
                        type: 'string',
                    },
                    ...commonProperties,
                },
                additionalProperties: false,
            },
            {
                properties: {
                    ...selectFirstSearchResult({ value: true }),
                    // setting a property to false means it is disallowed
                    defaultValue: false,
                    ...commonProperties,
                },
                additionalProperties: false,
            },
        ],
    },
};
